table.spc-03-3 {
    width: 100%;
    counter-reset: row-counter;
}
table.spc-03-3 td > div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
table.spc-03-3 td > div > span {
    margin: 0 !important;
    font-size: 1em !important;
}

table.spc-03-3 td ol {
    margin-bottom: 0;
    margin-right: 1em;
}

table.spc-03-3 tbody tr.sample td:first-child::before {
    content: '例）';
}
table.spc-03-3 tbody tr:not(.sample) td:first-child::before {
    counter-increment: row-counter;
    content: counter(row-counter, decimal) '）';
    display: inline-block;
}
