.grid-container {
    display: grid;
    grid-template-columns: 10em 1fr 10em; /* 两列，每列占 50% */
    grid-template-rows: 2em auto 2em; /* 三行 */
    gap: 0.5em; /* 行和列之间的间距 */
    align-items: center;
}
.grid-container-content {
    display: grid;
    grid-template-columns: 1fr 1fr; /* 两列，每列占 50% */
    grid-template-rows: auto auto auto; /* 三行 */
    gap: 0.1em; /* 行和列之间的间距 */
    border: 2px dashed black;
}
.grid-item img {
    border: 0 !important;
}
.grid-container > .grid-item:nth-child(4) {
    text-align: right;
}
.grid-item:nth-child(2) .row,
.grid-item:nth-child(8) .row {
    display: inline-flex;
    width: 100%;
    justify-content: space-evenly !important;
    align-items: center;
}

dl:not(.row) {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-evenly !important;
}

.spc-05-2 ol,
.spc-05-2 dl {
    margin: 0;
}
