ol.grid {
    display: grid !important;
    grid-template-columns: 1fr 1fr; /* 两列，每列占 50% */
    grid-template-rows: 2em 2em 2em; /* 三行 */
    gap: 0.5em; /* 行和列之间的间距 */
    align-items: center;
}
.big-image {
    margin-bottom: 0.5em;
}
.big-image img {
    border: 2px solid black;
    padding: 0.5em;
    margin-bottom: 0.5em;
}

