img {
    object-fit: contain;
    aspect-ratio: 1 / 1;
}

ol {
    margin-bottom: 1em;
}
ol > li {
    margin-bottom: 1em;
}
ol ol > li {
    margin: unset;
}

ol > li.sample::before {
    content: '例）';
    margin-left: -2em;
}
ol ol {
    list-style-type: none;
}
ol.topic > li {
    margin-bottom: 0.5em;
}
ol.topic > li:not(.sample) {
    counter-increment: outer-counter; /* 增加外层计数器 */
}
ol.topic > li:not(.sample)::before {
    content: counter(outer-counter, decimal) '）'; /* 使用全角数字 */
    font-family: 'Arial', sans-serif;
    margin-left: -24.9px;
}

ol[type='a'] {
    list-style-type: lower-alpha;
}
ol[type='a'] > li {
    margin-left: 1em;
}

/* selected */
ol[type='a'] > li.selected {
    position: relative;
}
ol[type='a'] > li.selected::after {
    content: '';
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid black;
    position: absolute;
    left: -1.55em;
    top: -0.05em;
}
ol ol[type='a'] > li > img {
    vertical-align: top;
    border: 1px solid black;
    padding: 0.2em;
}

ol[type='a'] > li:before {
    content: '' !important;
    margin-left: 0 !important;
    padding-right: 0 !important;
}

ol[type='a'].row {
    display: inline-flex;
}
ol[type='a'].row > li {
    margin-right: 2em;
    align-items: center;
}
ol[type='a'].border {
    border: dashed 1px #000 !important;
}

.title {
    display: flex;
    align-items: center;
    margin-bottom: 1em;
}
.title > b {
    margin-right: 1em;
}

li > u {
    margin-left: 1em;
}
li > u::before,
li > u::after {
    content: '　';
}

.font-white {
    color: transparent;
}

div.judgment {
    display: inline-flex;
}
div.judgment > span {
    width: 5em;
}
div.judgment > span:nth-child(even) {
    text-align: center;
    margin-right: 1em;
}

table {
    border-collapse: collapse;
}
table tr th,
table tr td {
    border: 1px solid black;
    height: 2em;
    padding: 0.5em;
}
table thead {
    background-color: #eee;
}
table div {
    display: inline-flex;
    justify-content: space-between;
    width: 5em;
}
table div > span:first-child {
    margin-bottom: 0.75em;
    margin-right: 1em;
}
table div > span:last-child {
    margin-top: 0.75em;
    font-size: 12px;
}

/* choose */
.choose-label::after {
    content: '→';
    padding-left: 1em;
}

/* parens */
span.parens {
    width: 5em
}
span.parens::before,
div.judgment > span:nth-child(even)::before {
    content: '（';
    padding-right: 0.5em;
}
span.parens::after,
div.judgment > span:nth-child(even)::after {
    content: '）';
    padding-left: 0.5em;
}
ol li ol li > span {
    display: inline-flex;

}

.images > ol {
    display: inline-flex;
}
.images > ol > li {
}
.images > ol > li::before {
    vertical-align: top;
}
.images img {
    border: 1px #000 solid;
    vertical-align: top;
    margin-left: calc(-2em - 1px);
    padding: 1em;
}

img {
    vertical-align: top;
}
ol > li > img {
    border: 2px #000 solid;
    padding: 1em;
}

.option-left {
    margin-left: 1em;
}
.option-right {
    margin-right: 1em;
}
